import React from "react"
import Button from "@material-ui/core/Button"
import CssBaseline from "@material-ui/core/CssBaseline"
import TextField from "@material-ui/core/TextField"
import FormControlLabel from "@material-ui/core/FormControlLabel"
import Checkbox from "@material-ui/core/Checkbox"
import Grid from "@material-ui/core/Grid"
import Typography from "@material-ui/core/Typography"
import { makeStyles } from "@material-ui/core/styles"
import Container from "@material-ui/core/Container"
import InputMask from "react-input-mask"
import Select from "@material-ui/core/Select"
import InputLabel from "@material-ui/core/InputLabel"
import MenuItem from "@material-ui/core/MenuItem"
import FormControl from "@material-ui/core/FormControl"
import Autocomplete from '@material-ui/lab/Autocomplete';
import { useForm } from "react-hook-form"
import { Link, useStaticQuery, graphql } from 'gatsby';
import axios from "axios"
import qs from 'querystring';
import Modal from 'react-modal';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

Modal.setAppElement('#___gatsby');
const modalStyles = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)"
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "120px auto",
    padding: 0,
    border: 0,
    backgroundColor: '#fff'
  }
};
const modalStylesMobile = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)"
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "20px auto",
    padding: 0,
    border: 0,
    backgroundColor: '#fff'
  }
};

const useStyles = makeStyles(theme => ({
  container: {
    paddingTop: 120,
    paddingBottom: 140,
    flexGrow: 1,
  },
  title: {
    marginBottom: theme.spacing(2),
    fontWeight: 500,
  },
  descripcion: {
    width: "80%",
    fontSize: 15,
    fontWeight: 500,
  },
  form: {
    width: "100%",
  },
  subtitulos: {
    fontSize: 15,
    fontWeight: "normal",
    fontStretch: "normal",
    fontStyle: "normal",
    lineHeight: 1.47,
    letterSpacing: "normal",
    color: "#000000",
    marginTop: 20,
    marginBottom: 0,
  },
  input: {
    marginTop: 8,
    marginBottom: 8,
  },
  select: {
    marginTop: 8,
    marginBottom: 8,
  },
  labelcheckbox: {
    fontSize: 14,
    marginBottom: 0,
  },
  link: {
    fontWeight: 600,
    color: "rgba(0, 0, 0, 0.87)",
  },
  submit: {
    borderRadius: 20,
    backgroundImage:
      "linear-gradient(0deg, rgba(50,53,64,1) 0%, rgba(181,181,181,1) 100%)",
    boxShadow: "0 7px 11px 0 rgba(0, 0, 0, 0.26)",
    width: 243,
    height: 40,
    color: "#ffffff",
    fontWeight: 500,
    letterSpacing: 4,
  },
}))

const mexicoStates = [
  "Aguascalientes",
  "Baja California",
  "Baja California Sur",
  "Campeche",
  "Coahuila de Zaragoza",
  "Colima",
  "Chiapas",
  "Chihuahua",
  "Distrito Federal",
  "Durango",
  "Guanajuato",
  "Guerrero",
  "Hidalgo",
  "Jalisco",
  "Mxico",
  "Michoacn de Ocampo",
  "Morelos",
  "Nayarit",
  "Nuevo Len",
  "Oaxaca",
  "Puebla",
  "Quertaro",
  "Quintana Roo",
  "San Luis Potos",
  "Sinaloa",
  "Sonora",
  "Tabasco",
  "Tamaulipas",
  "Tlaxcala",
  "Veracruz de Ignacio de la Llave",
  "Yucatn",
  "Zacatecas",
];


export default function Cotiza(props) {
  const classes = useStyles()
  const {allStrapiMarkets} = useStaticQuery(graphql`
  {
    allStrapiMarkets(filter: {name: {nin: "None"}}) {
      edges {
        node {
          name
          slug
          strapiId
        }
      }
    }
  }
  `)
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let [toggleModal, setToggleModal] = React.useState(false);
  let modalSuccess = React.useRef(true);
  const { handleSubmit, register, errors, setValue } = useForm()
  const onSubmit = values => {
    axios.post(`${props.context.apiHostName}/api/cotiza`, qs.stringify({
      name: values.name,
      lastName: values.paterno,
      phoneNumber: values.celular,
      email: values.email,
      company: values.company,
      state: values.state,
      sector: values.sector,
      otherSector: values.otherSector,
      description: values.description,
      novedades: values.novedades ? 'SI' : 'NO'
    }), {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then((response) => {
      modalSuccess.current = true;
      setToggleModal(true);
    }).catch(error => {
      modalSuccess.current = false
      setToggleModal(true);
    })
  }

  React.useEffect(() => {
    register({name: 'state'});
    register({name: 'sector'});
    register({name: 'otherSector'})
  }, [register])
  const onClose = () => setToggleModal(false);
  const autocompleteSector = allStrapiMarkets.edges.map(({node}) => node.name);
  return (
    <Container component="main" maxWidth="md" className={classes.container}>
      <CssBaseline />
      <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={5}>
            <Typography component="h1" variant="h4" className={classes.title}>
             Necesitas cotizar<br/>algn producto?
            </Typography>
            <Typography component="p" className={classes.descripcion}>
            Como podemos ayudarte?<br/>
            Tienes dudas, requieres informacin o alguna asesora?<br/>
            ThreeBond comprometido con ofrecerte el mejor servicio pone a tu disposicin esta seccin para que obtengas la informacin que requieres y las soluciones correctas a tus dudas respecto a los productos y aplicaciones.<br />
            Por favor detalla tus dudas y te contactaremos de inmediato.<br/>
            Somos la mejor opcin, acrcate a nosotros.
            </Typography>
          </Grid>
          <Grid item xs={12} md={7}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Typography component="p" className={classes.subtitulos}>
                  Ingresa tus datos personales
                </Typography>
                <TextField
                  fullWidth
                  id="name"
                  label="Primer nombre"
                  name="name"
                  autoComplete="name"
                  type="text"
                  className={classes.input}
                  inputProps={{
                    maxLength: 30,
                    ref: register({
                      required: "Required",
                    }),
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="paterno"
                  label="Apellido Paterno"
                  name="paterno"
                  autoComplete="paterno"
                  type="text"
                  className={classes.input}
                  inputProps={{
                    maxLength: 30,
                    ref: register({
                      required: "Required",
                    }),
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <InputMask mask="(99) 99-99-99-99-99">
                  {() => (
                    <TextField
                      fullWidth
                      id="celular"
                      label="Celular a 10 dgitos"
                      name="celular"
                      autoComplete="celular"
                      type="text"
                      className={classes.input}
                      inputProps={{
                        ref: register({
                          required: "Required",
                        }),
                      }}
                    />
                  )}
                </InputMask>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="email"
                  label="Correo electrnico@"
                  name="email"
                  autoComplete="email"
                  type="email"
                  className={classes.input}
                  inputProps={{
                    maxLength: 40,
                    ref: register({
                      required: "Required",
                    }),
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <Typography component="p" className={classes.subtitulos}>
                  Para ofrecerte un mejor servicio, ayudanos con los siguientes
                  datos
                </Typography>
                <TextField
                  fullWidth
                  id="company"
                  label="Compaia"
                  autoComplete="comp"
                  className={classes.input}
                  inputProps={{
                    name: 'company',
                    maxLength: 30,
                    ref: register(),
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <FormControl className={classes.select} fullWidth>
                  <InputLabel>Estado</InputLabel>
                  <Select id="state" name="state" onChange={(e)=> setValue('state', e.target.value)}>
                    {
                      mexicoStates.map(state => <MenuItem value={state}>{state}</MenuItem>)
                    }
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <FormControl className={classes.select} fullWidth>
                  <InputLabel>Sector</InputLabel>
                  <Select id="sector" name="sector" onChange={(e) => setValue('sector', e.target.value)}>
                    {
                      autocompleteSector.map(node => <MenuItem value={node}>{node}</MenuItem>)
                    }
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="description"
                  label="Agrega una descripcin de lo que te gustara cotizar"
                  name="description"
                  autoComplete="desc"
                  multiline
                  rowsMax="4"
                  className={classes.input}
                  inputProps={{
                    maxLength: 300,
                    ref: register(),
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox id="novedades" color="default" name="novedades" inputProps={{ref: register()}} />}
                  label={
                    <p className={classes.labelcheckbox}>
                      Quiero recibir novedades va correo electrnico
                    </p>
                  }
                />
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={
                    <Checkbox id="condiciones" color="default"  name="condiciones" inputProps={{ref: register({required: "Required",})}} />
                  }
                  label={
                    <p className={classes.labelcheckbox}>
                      Acepto{" "}
                      <a target="_blank" className={classes.link} href="/terminos-y-condiciones" >
                        Condiciones de Uso
                      </a>{" "}
                      y el{" "}
                      <a target="_blank" className={classes.link} href="/manejo-de-informacion-personal" >
                        Aviso de Privacidad
                      </a>{" "}
                      de Thre Bond
                    </p>
                  }
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  className={classes.submit}
                >
                  ENVIAR
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </form>
      <Modal
        isOpen={toggleModal}
        style={matchMobile ? modalStylesMobile : modalStyles}
        contentLabel="MODAL"
        className="cotiza-modal"
      >
        <div>
          <div className="close-modal-container">
            <button className="close-modal" onClick={onClose}>
              <FontAwesomeIcon
                icon={["fas", "times"]}
                style={{ color: "#000000" }}
              />
            </button>
          </div>
          {
            modalSuccess.current ?
              <p>Muchas gracias por proporcionarnos tus datos! Alguien se pondra en contacto contigo.</p> :
              <p>Lo sentimos ha ocurrido un error. Intentelo ms tarde.</p>
          }

        </div>
      </Modal>
    </Container>
  )
}
